"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Smartphone } from "lucide-react"
import { useState } from "react"
import { useAuth } from "@/contexts/auth-context"
import { useData } from "@/contexts/data-context"
import { useToast } from "@/hooks/use-toast"

type PaymentMethod = "crystalpay"

export function PaymentMethods() {
  const [amount, setAmount] = useState("")
  const { user } = useAuth()
  const { addTransaction } = useData()
  const { toast } = useToast()

  const paymentOptions = [
    {
      id: "crystalpay" as PaymentMethod,
      name: "CrystalPay (доллары США)",
      icon: Smartphone,
      description: "Оплата напрямую в USD через криптовалюты (BTC, ETH, USDT) или СБП.",
      fee: "до 5%",
    },
  ]

  const handlePayment = async () => {
    if (!user || !amount || Number.parseFloat(amount) < 10) {
      toast({
        title: "Ошибка",
        description: "Минимальная сумма: $10 USD",
        variant: "destructive",
      });
      return;
    }

    let roundedAmount = Number.parseFloat(amount);
    if (isNaN(roundedAmount) || roundedAmount <= 0) {
      toast({
        title: "Ошибка",
        description: "Некорректная сумма (только USD!)",
        variant: "destructive",
      });
      return;
    }
    roundedAmount = Number(roundedAmount.toFixed(2));

    try {
      toast({
        title: "CrystalPay: обработка платежа...",
        description: "Пожалуйста, подождите",
      });

      const response = await fetch("/api/crystalpay", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          amount: roundedAmount,
          description: `Пополнение через CrystalPay (USD)`,
          currency: "USD",
          userId: user.id,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Unknown error");
      }

      addTransaction({
        userId: user.id,
        type: "topup",
        amount: roundedAmount,
        method: "crystalpay",
        status: "pending",
        description: `Пополнение через CrystalPay (USD)`,
      });

      toast({
        title: "Инвойс создан!",
        description: `Перенаправляем на оплату CrystalPay...`,
      });

      window.location.href = data.url;
    } catch (err) {
      toast({
        title: "Ошибка",
        description: (err as Error).message,
        variant: "destructive",
      });
    }
  };

  let totalToPayFormatted = "0.00";
  if (amount && Number.parseFloat(amount) >= 10) {
    totalToPayFormatted = `$${Number.parseFloat(amount).toFixed(2)} (USD, комиссия до 5%)`;
  }

  return (
    <div className="card-gradient rounded-xl p-6 lg:p-8 border border-border/50">
      <h2 className="text-2xl font-bold text-foreground mb-6">
        Пополнить баланс в долларах США (CrystalPay)
      </h2>

      <div className="space-y-6">
        <div className="space-y-3">
          <Label className="text-foreground">Оплата только в USD через CrystalPay</Label>
          <div className="grid grid-cols-1 gap-4">
            {paymentOptions.map((option) => {
              const Icon = option.icon;
              return (
                <button
                  key={option.id}
                  disabled
                  className="p-4 rounded-lg border-2 border-primary bg-primary/10 glow-effect text-left opacity-90"
                >
                  <div className="flex items-start gap-3">
                    <div className="w-10 h-10 rounded-lg flex items-center justify-center bg-primary/20">
                      <Icon className="w-5 h-5 text-primary" />
                    </div>
                    <div className="flex-1">
                      <p className="font-semibold text-foreground mb-1">{option.name}</p>
                      <p className="text-xs text-muted-foreground mb-2">{option.description}</p>
                      <p className="text-xs text-primary">Комиссия: {option.fee}</p>
                    </div>
                  </div>
                </button>
              );
            })}
          </div>
        </div>

        <div className="space-y-2">
          <Label htmlFor="amount" className="text-foreground">
            Сумма пополнения (в долларах USD)
          </Label>
          <Input
            id="amount"
            type="number"
            min="10"
            step="0.01"
            placeholder="50.00"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            className="bg-background/50 border-border/50 focus:border-primary"
            inputMode="decimal"
          />
          <p className="text-xs text-muted-foreground">Минимальная сумма: $10.00 USD</p>
        </div>

        <div className="space-y-4 pt-4 border-t border-border/50">
          <div className="bg-muted/20 rounded-lg p-4 border border-border/30">
            <p className="text-sm text-muted-foreground mb-2">
              Оплата только в долларах США через CrystalPay. Комиссия до 5%.
            </p>
            <p className="text-lg font-semibold text-foreground">Итого к оплате: {totalToPayFormatted}</p>
          </div>
        </div>

        <Button
          className="w-full bg-primary hover:bg-primary/90"
          disabled={!amount || Number.parseFloat(amount) < 10}
          onClick={handlePayment}
        >
          CrystalPay оплата (USD)
        </Button>
      </div>
    </div>
  );
}